import React, { Component } from 'react';
import Coverflow from 'react-coverflow';
import YouTube from 'react-youtube';
import { Row, Col } from 'reactstrap';

import img2 from '../../assets/images/img2.png';
import portfolio1 from '../../assets/images/portfolio1.jpg';
import portfolio2 from '../../assets/images/portfolio2.jpg';
import portfolio3 from '../../assets/images/portfolio3.jpg';
import portfolio4 from '../../assets/images/portfolio4.jpg';

import './Portfolio.scss';

const opts = {
    height: '390',
    width: '640',
    playerVars: { // https://developers.google.com/youtube/player_parameters
      autoplay: 1
    }
  };

const videoIDs = [
    'oKkexeqhip4',
    'hUJ_3vZhZK0',
    'NIw686oeIWI',
    'wRJ07fthwyg',
]

class Portfolio extends Component {
    constructor(props) {
        super(props);
    
        this.state = {
            active: 0,
            closed: true,
            videoID: videoIDs[0],
            textTranslateY: 0,
        };

        this.handleClick = this.handleClick.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.onReady = this.onReady.bind(this);
        this.onScroll = this.onScroll.bind(this);
    }
    componentDidMount() {
        window.addEventListener('scroll', this.onScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.onSrcoll);
    }

    onScroll() {
        const { textTranslateY } = this.state;

        this.setState({
            textTranslateY: window.scrollY,
        })
    }

    handleClick(id) {
        this.setState({
          active: id,
          videoID: videoIDs[id],
          closed: false,
        });
      }

    handleClose() {
        this.setState({
            closed: true,
        });
    }

      onReady(event) {
        // access to player in all event handlers via event.target
        event.target.playVideo();
      }

    render() {
        const { closed, videoID, textTranslateY } = this.state;
        const youtubeStyle = closed ? 'closed' : 'open';
      return (
        <Row className="portfolio">
            <Col>
            <h1>Our Works</h1>
            <img className="bg" src={img2} alt='img2' style={{ transform: `translateY(${-400 + textTranslateY * 0.1}px)`, opacity: -4.5 + textTranslateY * 0.003}}/>
            <Coverflow
                width={960}
                height={480}
                displayQuantityOfSide={2}
                navigation={false}
                enableHeading={false}
                active={this.state.active}
                >
                <div
                    onClick={() => this.handleClick(0)}
                    onKeyDown={() => {}}
                    role="menuitem"
                    tabIndex="0"
                >
                    <img style={{ display: 'block', width: '100%' }} src={portfolio2} alt='portfolio2' />
                </div>
                <img onClick={() => this.handleClick(1)} src={portfolio3} alt='portfolio3' />
                <img onClick={() => this.handleClick(2)} src={portfolio4} alt='portfolio4' />
                <img onClick={() => this.handleClick(3)} src={portfolio1} alt='portfolio1' />
            </Coverflow>
            </Col>
            {
                closed ?
                 null :
                 <Col
                    className={`youtube ${youtubeStyle}`}
                    onClick={this.handleClose}
                >
                    <YouTube
                        videoId={videoID}
                        opts={opts}
                        onReady={this.onReady}
                    />
                </Col>
            }
            
        </Row>
      );
    }
  }
  
  export default Portfolio;