import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

import {PricingTable, PricingSlot, PricingDetail} from 'react-pricing-table';

import Mascot from '../../assets/images/Mascot.png';

import './Pricing.scss';

class Pricing extends Component {
    constructor(props) {
        super(props);

        this.state = {
            textTranslateY: 0,
        }
    }

    render() {
      return (
        <Row className="pricing" noGutters>
            <Col xs={12}>
                <h1>Price List</h1>
            </Col>
            <Col xs={12}>
            <img className="mascot" src={Mascot} />
            <PricingTable  highlightColor='#45b5aa'>
                <PricingSlot onClick={this.submit} buttonText='SIGN UP' title='Marriage Proposal Animation' priceText='S$800 first minute'>
                    <PricingDetail> <b>S$400</b> subsequent mins</PricingDetail>
                    <PricingDetail> <b>High Quality</b> Animated Short Story</PricingDetail>
                    <PricingDetail> <b>Personalized Character Design</b> (up to 3x)</PricingDetail>
                    <PricingDetail> Two personalized outfit for each, bride and groom </PricingDetail>
                    <PricingDetail> Customized Background Design for each scene </PricingDetail>
                    <PricingDetail> Sound FX and Background Music </PricingDetail>
                    <PricingDetail> Full HD </PricingDetail>
                </PricingSlot>
                <PricingSlot highlighted onClick={this.submit} buttonText='SIGN UP' title='Wedding Animation' priceText='S$800 first minute'>
                <PricingDetail> <b>S$400</b> subsequent mins</PricingDetail>
                    <PricingDetail> <b>High Quality</b> Animated Short Story</PricingDetail>
                    <PricingDetail> <b>Personalized Character Design</b> (up to 3x)</PricingDetail>
                    <PricingDetail> Two personalized outfit for each, bride and groom </PricingDetail>
                    <PricingDetail> Customized Background Design for each scene </PricingDetail>
                    <PricingDetail> Sound FX and Background Music </PricingDetail>
                    <PricingDetail> Full HD </PricingDetail>
                </PricingSlot>
                <PricingSlot  onClick={this.submit} buttonText='SIGN UP' title='E-Invitation (GIF)' priceText='S$150 loop'>
                    <PricingDetail> Animated characters and BG elements</PricingDetail>
                    <PricingDetail> Personalized Character Design (2x)</PricingDetail>
                    <PricingDetail> Customized Background Design</PricingDetail>
                    <PricingDetail> Optimized for social media</PricingDetail>
                </PricingSlot>
                <PricingSlot  onClick={this.submit} buttonText='SIGN UP' title='Save the date' priceText='$250 custom'>
                    <PricingDetail> Short Animation Montage</PricingDetail>
                    <PricingDetail> Personalized Character Design (2x)</PricingDetail>
                    <PricingDetail> Customized Background Design</PricingDetail>
                    <PricingDetail> Optimized for social media</PricingDetail>
                </PricingSlot>
            </PricingTable>
            </Col>
        </Row>
      );
    }
  }
  
  export default Pricing;