import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

import img1 from '../../assets/images/img1.png';

import './About.scss';

class About extends Component {
    constructor(props) {
        super(props);

        this.state = {
            textTranslateY: 0,
        }

        this.onScroll = this.onScroll.bind(this);
    }
    componentDidMount() {
        window.addEventListener('scroll', this.onScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.onSrcoll);
    }

    onScroll() {
        const { textTranslateY } = this.state;

        this.setState({
            textTranslateY: window.scrollY,
        })
    }

    render() {
        const { textTranslateY } = this.state;

      return (
        <Row className="about d-flex justify-content-center align-items-center" noGutters>
            <Col className="desc" xs={6}>
                <h6 className="text1" style={{ transform: `translateX(100px) translateY(${350 -textTranslateY * 0.5}px)`}}>At Infinite Frontier, quality is priority. We are dedicated to produce the highest quality of storytelling through animation, with extraordinary attention to detail in developing and planning your story.</h6>
                <h6 className="text2" style={{ transform: `translateX(100px) translateY(${300 -textTranslateY * 0.4}px)`}}>To provide remarkable animated storytelling in the most simplistic and innovative way. Our aim is to create an experience to relive your journey that serves as an everlasting, heartfelt memory.</h6>
            </Col>
            <Col className="image" xs={6}>
                <img style={{ transform: `translateX(-200px) translateY(${-200 + textTranslateY * 0.15}px)`, opacity: -1.8 + textTranslateY * 0.003}} src={img1} />
            </Col>
        </Row>
      );
    }
  }
  
  export default About;